// main
#mainCont {
    height: 100vh;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main__cont {
    color: #fff;
    font-size: 10vw;
    line-height: 0.85;
    letter-spacing: -0.0275em;
    transition: all 2s;
    font-family: 'Saol Display Regular';
    text-transform: uppercase;
}


//header
#aboutCont {
    background-color: #f0eeeb;
}

.about-cont .about {
    padding-bottom: 250px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.about-cont .about:nth-child(1) .about-img {
    flex: 0 0 100%;
}

.about-cont .about:nth-child(1) .about-txt {
    flex: 0 0 100%;
}

.about-cont .about:nth-child(1) .about-txt h2 {
    margin-top: 2em;
}

.about-cont .about:nth-child(2) .about-img {
    flex: 0 0 64%;
}

.about-cont .about:nth-child(2) .about-txt {
    flex: 0 0 30%;
}

.about-cont .about:nth-child(3) {
    flex-direction: row-reverse;
}

.about-cont .about:nth-child(3) .about-img {
    flex: 0 0 54%;
}

.about-cont .about:nth-child(3) .about-txt {
    flex: 0 0 40%;
}

.about-cont .about .about-txt {
    font-family: 'Chosunilbo_myungjo';
}

.about-cont .about .about-txt h2 {
    margin: 0 0 1em;
    font-size: 1.625rem;
}

.about-cont .about .about-txt p {
    line-height: 1.6;
    font-size: 16px;
}



/* referCont */
#referCont {
    background-color: #f0eeeb;
}

.refer-cont {
    padding-bottom: 200px;
    padding: 150px 40px 100px;
    min-height: 100vh;
}

.refer-cont .refer-title li {
    display: inline;
    font-family: 'Rajdhani';
}

.refer-cont .refer-title li a {
    font-size: 1.2rem;
    display: inline-block;
    padding: 0.5rem 2rem;
    border: 1px solid #222;
    border-radius: 5px;
    margin-bottom: 1rem;
    margin-right: 10px;
}

.refer-cont .refer-title li.active a {
    background: #000;
    color: #fff;
}

.refer-cont .refer-table {
    border-bottom: 2px solid #000;
    margin-top: 1.2rem;
}

.refer-cont .refer-table li {
    margin: 0;
    list-style: none;
    border-top: 1px solid #000;
    position: relative;
    transition: all 0.3s;
}

.refer-cont .refer-table li:hover {
    background: rgba(0, 0, 0, 0.081);
}

.refer-cont .refer-table li span {
    display: inline-block;
    padding: 25px 10px;
    font-size: 14px;
    font-family: 'S-CoreDream-3Light';
    box-sizing: border-box;
}

.refer-cont .refer-table li span.alpha {
    position: absolute;
    left: 0;
    top: 0;
}

.refer-cont .refer-table li span.attr {
    padding-left: 10%;
    width: 35%;
}

.refer-cont .refer-table li span.desc {
    width: 64.5%;
}



/* youtubeCont */
#youtubeCont {
    background-color: #f0eeeb;
}

.youtube-cont {
    padding-bottom: 200px;
}

.site {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 200px;
}

.site h2 {
    font-size: 40px;
    padding-bottom: 20px;
    flex: 0 0 100%;
    font-family: 'Chosunilbo_myungjo';
}

.site .left {
    flex: 0 0 35%;
}

.site .right {
    font-family: 'Chosunilbo_myungjo';
    font-size: 18px;
    flex: 0 0 60%;
}

.site .right dl {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.site .right dt {
    flex: 0 0 20%;
}

.site .right dd {
    flex: 0 0 80%;
    padding-bottom: 30px;
}

.youtube-search {
    font-family: 'Chosunilbo_myungjo';
}
.youtube-search .title h2 {
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 30px;
    font-size: 40px;
}
.youtube-search .title input {
    border: 0;
    padding: 10px;
    vertical-align: 5px;
    font-family: 'Chosunilbo_myungjo';
}
.youtube-search .title button {
    border: 0;
    padding: 10px 30px;
    background: #000;
    color: #fff;
    vertical-align: 5px;
    margin-left: 5px;
    font-family: 'Chosunilbo_myungjo';
}
.youtube-search .list ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.youtube-search .list li {
    flex : 0 0 19%;
    margin-bottom: 2%;
}
.youtube-search .list li strong {
    margin-top: 5px;
    display: block;
}



/* scriptCont */
#scriptCont {
    background-color: #f0eeeb;
}

.script-cont {
    padding-bottom: 0px;
}

.script {
    padding-bottom: 200px;
}

.script h2 {
    font-family: 'Chosunilbo_myungjo';
    font-size: 40px;
    padding-bottom: 10px;
}

.script .view {
    width: 100%;
}

.script .desc {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}

.script .desc h3 {
    flex: 0 0 100%;
    font-size: 30px;
    line-height: 2em;
    margin-top: 20px;
    font-family: 'Chosunilbo_myungjo';
}

.script .desc>div {
    flex: 0 0 31%;
    font-family: 'Chosunilbo_myungjo';
}

.script .desc>div.num span {
    border: 1px solid #9B9B9B;
    width: 50px;
    height: 50px;
    display: inline-block;
    border-radius: 50%;
    text-align: center;
    line-height: 48px;
    margin: 0px 0px 10px 7px;
    color: #030406;
}

.script .desc>div.num span.active {
    border-style: dashed
}


/* contactCont */
#contactCont {
    height: 100vh;
    background-color: #000;
}

.contact-cont {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    white-space: nowrap;
}

.contact-cont div {
    font-size: 100px;
    font-family: "Saol Display Regular";
    font-weight: normal;
    padding-top: 1.6vw;
    text-transform: uppercase;
    color: #fff;
    font-size: 180px;
    line-height: 130px;
}





/* 포트폴리오 페이지 
.port__cont 
.port__item 
.port__item__img
.port__item__txt
*/
#portCont {
    background: #000;
    padding-bottom: 200px;
}

.port__cont {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.port__item {
    flex: 0 0 32%;
    margin-bottom: 2%;
}

.port__item__txt {
    padding: 35px 35px 25px 35px;
    background: #151517;
}

.port__item__txt h2 {
    font-family: 'Saol Display Regular';
    font-size: 50px;
    line-height: 1;
    color: #fff;
}

.port__item__txt p {
    font-size: 16px;
    color: #b0aead;
    font-family: 'Rajdhani';
}